/* 图片放大器 */
import './index.less';
import React from 'react';
import { Empty, Tag, Divider } from 'antd';
import Zmage from 'react-zmage';

class Zmagemagnifier extends React.Component {
  mouseEnter = () => {
    this.setState({
      magnifierOff: true
    });
  };
  mouseLeave = () => {
    this.setState({
      magnifierOff: false
    });
  };
  mouseMove = event => {
    let e = event.nativeEvent;
    this.calculationBlock(e.offsetX, e.offsetY);
  };
  calculationBlock = (offsetX, offsetY) => {
    let params = JSON.parse(JSON.stringify(this.state.params));
    let cssStyle = JSON.parse(JSON.stringify(this.state.cssStyle));
    if (offsetX < 50) {
      offsetX = 50;
    }
    if (offsetX > 350) {
      offsetX = 350;
    }
    if (offsetY < 50) {
      offsetY = 50;
    }
    if (offsetY > 350) {
      offsetY = 350;
    }
    cssStyle.mouseBlock.left = parseFloat(offsetX - 50) + 'px';
    cssStyle.mouseBlock.top = parseFloat(offsetY - 50) + 'px';
    cssStyle.imgStyle2.left = parseFloat(-(offsetX - 50) * params.scale) + 'px';
    cssStyle.imgStyle2.top = parseFloat(-(offsetY - 50) * params.scale) + 'px';
    this.setState({
      cssStyle: cssStyle
    });
  };
  initParam = () => {
    let cssStyle = JSON.parse(JSON.stringify(this.state.cssStyle));
    let params = JSON.parse(JSON.stringify(this.state.params));
    cssStyle.imgContainer.width = params.width + 'px';
    cssStyle.imgContainer.height = params.height + 'px';
    cssStyle.magnifierContainer.width = params.width + 'px';
    cssStyle.magnifierContainer.height = params.height + 'px';
    cssStyle.magnifierContainer.left = params.width + 'px';
    cssStyle.imgStyle2.width = (params.width*params.scale) + 'px';
    cssStyle.imgStyle2.height = (params.height*params.scale) + 'px';
    cssStyle.imgStyle2.transform = 'scale(' + params.scale + ')';
    this.setState({
      cssStyle: cssStyle
    });
  };
  updataImg = (props) => {
    this.setState({
      minImg: props.minImg,
      maxImg: props.maxImg
    });
  };
  handleImageLoaded = (e) => {
    this.setState({ imgLoad: true });
  };
  handleImageErrored = () => {
    this.setState({ imgLoad: false });
  };
  constructor(props) {
    super(props);
    this.state = {
      params: {
        scale: 1.5,
        width: '400',
        height: '400'
      },
      minImg: '',
      maxImg: '',
      magnifierOff: false,
      imgLoad: false,
      cssStyle: {
        imgContainer: {
          width: '400px',
          height: '400px',
          border: '1px solid #ccc',
          cursor: 'move',
          position: 'relative'
        },
        mouseBlock: {
          position: 'absolute',
          top: '0',
          left: '0',
          width: '100px',
          height: '100px',
          background: 'rgba(0,0,0,0.1)',
          zIndex: 99
        },
        maskBlock: {
          position: 'absolute',
          top: '0',
          left: '0',
          width: '100%',
          height: '100%',
          background: 'rgba(0,0,0,0)',
          zIndex: 100
        },
        magnifierContainer: {
          position: 'absolute',
          left: '800px',
          top: '0',
          width: '400px',
          height: '400px',
          border: '1px solid #ccc',
          overflow: 'hidden',
          zIndex: 98
        },
        imgStyle: {
          width: '100%',
          height: '100%'
        },
        imgStyle2: {
          width: (400*1.5)+'px',
          height: (400*1.5)+'px',
          position: 'absolute',
          top: 0,
          left: 0,
          transform: 'scale(1.5)',
          transformOrigin: 'top left'
        }
      }
    };
  };
  componentDidMount() {
    const img = require('./img.jpg');
    this.initParam();
    this.updataImg({
      minImg: img,
      maxImg: img
    });
  };
  render() {
    const { cssStyle, magnifierOff, minImg, maxImg, imgLoad } = this.state;
    return (
      <div className="zmage">
        <Divider orientation="left">
          <Tag color="blue">图片放大器</Tag>
        </Divider>
        <div className="zmageContainer">
          <div style={cssStyle.imgContainer}>
            <Zmage style={cssStyle.maskBlock}
              src={minImg}
              onMouseEnter={this.mouseEnter}
              onMouseLeave={this.mouseLeave}
              onMouseMove={this.mouseMove} />
            {magnifierOff && <div style={cssStyle.mouseBlock} />}
          </div>
          {
            magnifierOff && (
              <div style={cssStyle.magnifierContainer}>
                <img alt=""
                  style={cssStyle.imgStyle2}
                  src={maxImg}
                  onLoad={this.handleImageLoaded.bind(this)}
                  onError={this.handleImageErrored.bind(this)} />
                  {!imgLoad && <Empty description="暂无图片" />}
              </div>
            )
          }
        </div>
      </div>
    );
  }
}

export default Zmagemagnifier;
